import {
  NetBase,
} from '../NetBase'
import {
  Topic,
} from '../../common/constant'
import {
  charCodes2Json,
  json2ArrayBuffer,
  str2ArrayBuffer,
} from '../../common/helper'
import {
  cmdBuf2Json,
} from '../../common/protobuf/protoUtil'
import {
  log,
} from '../../common/console'
import {
  ControllerRequest,
  ImData,
} from '../../common/types/apply'

enum Platform {
  Unknow = 0,
  Win = 1,
  Android = 2,
  iOS = 3,
  Web = 4,
  SeewoOS = 5,
  Mac = 6,
  Linux = 7
}

interface AuthUser {
  timestamp: number;
  signature: string;
  token: string;
}

interface CbData {
  traceid: string;
  data: ArrayBuffer;
  topic: string;
  code: number;
  message: string;
  extend: ArrayBuffer;
}

interface ApiPack {
  method: string;
  time: number;
  data: ArrayBuffer;
}

interface ApiPackAck {
  method: string;
  time: number;
  data: ImData;
}

interface ParseCallAck {
  traceid: string;
  topic: string;
  data: ApiPackAck;
  extend: any;
  code: number;
  message: string;
}

interface Auth {
  user: AuthUser;
}

interface AuthInfo {
  auth: Auth;
}
interface InitInfo {
  host: string;
  appid: string;
  uid: string;
  pwd: string;
  platform: Platform;
  heartbeat: number;
  isAutoReconnect: boolean;
  authInfo: AuthInfo;
  will: string;
}

export class Controller {
  public netBase: NetBase = new NetBase();

  init(initInfo: InitInfo) {
    const newInitInfo = {
      data: json2ArrayBuffer(initInfo.authInfo),
      will: str2ArrayBuffer(initInfo.will),
      host: initInfo.host,
      appid: initInfo.appid,
      uid: initInfo.uid,
      pwd: initInfo.pwd,
      platform: initInfo.platform,
      heartbeat: initInfo.heartbeat,
      isAutoReconnect: initInfo.isAutoReconnect,
    }
    this.netBase.init(newInitInfo)
    return this
  }

  /**
   * 获取底层连接状态
   */
  getIsConnected() {
    return this.netBase.getIsConnected()
  }

  /**
   * 监听连接成功事件
   * @param {function} fn 回调函数
   */
  onConnect(fn) {
    this.netBase.onConnect((data) => {
      log('_MSSDK_Controller_ onConnect data:', data)
      fn && fn(data)
    })
  }

  /**
   * 监听断开连接事件
   * @param {function} fn 回调函数
   */
  onDisconnect(fn) {
    this.netBase.onDisconnect((data) => {
      log('_MSSDK_Controller_ onDisconnect data:', data)
      fn && fn(data)
    })
  }

  /**
   * 启动连接时的各种监听
   */
  start() {
    this.netBase.start()
    return this
  }

  /**
   * 发送PUSH数据
   * @param {object} rtmqData PUSH类型数据
   */
  send({
    rtmqData,
    onSuccess,
    onFail,
    onTimeout,
  }: ControllerRequest) {
    log('_MSSDK_Controller_ send rtmqData:', rtmqData)

    this.netBase.send({
      rtmqData,
      onSuccess: (respRtmqData) => {
        const context = {
          traceid: respRtmqData.traceid,
          topic: respRtmqData.topic,
        }
        log('_MSSDK_Controller_ send onSuccess respRtmqData:', respRtmqData)
        onSuccess && onSuccess(null, context)
      },
      onFail: (respRtmqData) => {
        const context = {
          traceid: respRtmqData.traceid,
          topic: respRtmqData.topic,
        }
        log('_MSSDK_Controller_ send onFail respRtmqData:', respRtmqData)
        onFail && onFail({
          code: respRtmqData.code,
          message: respRtmqData.message,
        }, context)
      },
      onTimeout: (rtmqData) => {
        log('_MSSDK_Controller_ send onTimeout')
        const context = {
          traceid: rtmqData.traceid,
          topic: rtmqData.topic,
        }
        onTimeout && onTimeout({
          code: -2,
          message: '服务器超时',
        }, context)
      },
    })

    return this
  }

  /**
   * 发送CALL数据
   * @param {object} param0 请求数据的配置
   */
  call({
    rtmqData,
    onSuccess,
    onFail,
    onTimeout,
  }: ControllerRequest) {
    log('_MSSDK_Controller_ call rtmqData:', rtmqData)

    this.netBase.call({
      rtmqData,
      onSuccess: (respRtmqData) => {
        const respRtmqData2 = this._recvDataParse(respRtmqData)
        const context = {
          traceid: respRtmqData2.traceid,
          topic: respRtmqData2.topic,
          time: respRtmqData2.data.time,
          method: respRtmqData2.data.method,
        }

        log('_MSSDK_Controller_ call onSuccess respRtmqData2:', respRtmqData2)
        onSuccess && onSuccess(respRtmqData2.data.data, context)
      },
      onFail: (respRtmqData) => {
        const respRtmqData2: {
          data: ApiPackAck;
          traceid: string;
          topic: string;
          code: number;
          message: string;
        } = this._recvDataParse(respRtmqData)
        const context = {
          traceid: respRtmqData2.traceid,
          topic: respRtmqData2.topic,
          method: respRtmqData2.data.method,
        }
        log('_MSSDK_Controller_ call onFail respRtmqData2:', respRtmqData2)
        onFail && onFail({
          code: respRtmqData2.code,
          message: respRtmqData2.message,
        }, context)
      },
      onTimeout: (rtmqData) => {
        log('_MSSDK_Controller_ call onTimeout')
        const context = {
          traceid: rtmqData.traceid,
          topic: rtmqData.topic,
          method: rtmqData.data.method,
        }
        onTimeout && onTimeout({
          code: -2,
          message: '服务器超时',
        }, context)
      },
    })
    return this
  }

  /**
   * 断开连接时的各种监听
   */
  stop() {
    this.netBase.stop()
    return this
  }

  /**
   * 监听服务端主动推送事件
   * @param {function} fn 接收服务端推送的回调
   */
  onRecv(fn) {
    this.netBase.onRecv((rtmqData) => {
      const parseRtmqData = this._recvDataParse(rtmqData)

      const apiPack = parseRtmqData.data
      const context = {
        traceid: parseRtmqData.traceid,
        topic: parseRtmqData.topic,
        time: apiPack.time,
        method: parseRtmqData.data.method,
      }
      log('_MSSDK_Controller_ onRecv parseRtmqData:', parseRtmqData)
      fn && fn(apiPack, context)
    })
    return this
  }

  _recvDataParse<T extends {topic: string; data: ArrayBuffer; traceid: string}>(rtmqData: T): T & {
    data: ApiPackAck;
    traceid: string;
    topic: string;
  } {
    log('_MSSDK_Controller_ _recvDataParse rtmqData:', rtmqData)
    let parseRtmqData: {
      data: ApiPackAck;
      traceid: string;
      topic: string;
    }
    switch(rtmqData.topic) {
      case Topic.IM_DATA:
      case Topic.PUSH_DATA:
        parseRtmqData = Object.assign({}, rtmqData, {
          data: charCodes2Json(rtmqData.data) as ApiPackAck,
          traceid: rtmqData.traceid,
        })
        break
      case Topic.IM_CMD:
        parseRtmqData = Object.assign({}, rtmqData, {
          data: cmdBuf2Json(rtmqData.data) as ApiPackAck,
        })
        break
      default:
    }
    log('_MSSDK_Controller_ _recvDataParse return parseRtmqData:', parseRtmqData)

    return Object.assign({}, rtmqData, parseRtmqData)
  }
}
