class ReadBlock {
  pos: number;
  dv: DataView;

  constructor(buffer: ArrayBuffer) {
    this.pos = 0
    this.dv = new DataView(buffer)
  }

  ReadInt16Field: () => number = () => {
    const val0 = this.dv.getUint8(this.pos++)
    const val1 = this.dv.getUint8(this.pos++)
    const ret = (val1 << 8) | val0
    return ret
  }

  ReadInt32Field: () => number = () => {
    const val0 = this.dv.getUint8(this.pos++)
    const val1 = this.dv.getUint8(this.pos++)
    const val2 = this.dv.getUint8(this.pos++)
    const val3 = this.dv.getUint8(this.pos++)
    const ret = (val3 << 24) | (val2 << 16) | (val1 << 8) | val0
    return ret
  }

  ReadInt64Field: () => number = () => {
    let ret = 0
    let step = 1
    for (let i = 0; i < 8; i++) {
      ret += (step * this.dv.getUint8(this.pos++))
      step *= 256
    }
    return ret
  }

  ReadStringField: () => string = () => {
    let str = ''
    const len = this.ReadInt16Field()
    for (let i = 0; i < len; i++) {
      str += String.fromCharCode(this.dv.getUint8(this.pos++))
    }
    return str
  }

  // TODO: 函数改名为ArrayBuffer
  ReadArrayBufferField: () => ArrayBuffer = () => {
    // const arr = []
    // const len = this.ReadInt16Field()
    // for (let i = 0; i < len; i++) {
    //   arr.push(this.dv.getUint8(this.pos++))
    // }
    // return new Uint8Array(arr).buffer
    const len = this.ReadInt16Field()
    const buf = this.dv.buffer.slice(this.pos, this.pos + len)
    this.pos += len
    return buf
  }
}

class WriteBlock {
  arr: Array<number>;

  constructor() {
    this.arr = []
  }

  WriteInt16Field: (val: number) => void = val => {
    this.arr.push(val & 0xFF)
    this.arr.push((val >> 8) & 0xFF)
  }

  WriteInt32Field: (val: number) => void = val => {
    this.arr.push(val & 0xFF)
    this.arr.push((val >> 8) & 0xFF)
    this.arr.push((val >> 16) & 0xFF)
    this.arr.push((val >> 24) & 0xFF)
  }

  WriteInt64Field: (val: number) => void = val => {
    let ret = val
    for (let i = 0; i < 8; i++) {
      this.arr.push(ret & 0xFF)
      ret /= 256
    }
  }

  WriteStringField: (str: string) => void  = str => {
    this.WriteInt16Field(str.length)
    for (let i = 0; i < str.length; ++i) {
      this.arr.push(str.charCodeAt(i))
    }
  }
  WriteArrayBufferField: (buf: ArrayBuffer) => void = buf => {
    // TODO:这里的算法可以优化？
    this.WriteInt16Field(buf.byteLength)
    const ui8 = new Uint8Array(buf)
    for (let i = 0; i < ui8.length; i++) {
      this.arr.push(ui8[i])
    }
  }

  getArrayBuffer: () => ArrayBuffer = () => {
    return new Uint8Array(this.arr).buffer
  }
}

class CommandPack {
  method: string;
  data: ArrayBuffer;
  extend: ArrayBuffer;


  constructor(config?) {
    this.method = get(config, 'method', '')
    this.data = get(config, 'data', new Uint8Array([]).buffer)
    this.extend = get(config, 'extend', new Uint8Array([]).buffer)
  }

  Decode: (buffer: ArrayBuffer) => {
    method: string;
    data: ArrayBuffer;
    extend: ArrayBuffer;
  } = buffer => {
    const bk = new ReadBlock(buffer)
    // this.method = bk.ReadStringField();
    // this.data = bk.ReadArrayBufferField();
    // this.extend = bk.ReadArrayBufferField();

    return {
      method: bk.ReadStringField(),
      data: bk.ReadArrayBufferField(),
      extend: bk.ReadArrayBufferField(),
    }
  }

  Encode: () => ArrayBuffer = () => {
    const bk = new WriteBlock()
    bk.WriteStringField(this.method)
    bk.WriteArrayBufferField(this.data)
    bk.WriteArrayBufferField(this.extend)
    return bk.getArrayBuffer()
  }
}

class SingleCmdReq {
  toAppid: string;
  toUid: string[];
  toPlatform: number[];
  msg: ArrayBuffer;
  ttl: number;

  constructor(config) {
    this.toAppid = get(config, 'toAppid', '')
    this.toUid = get(config, 'toUid', [])
    this.toPlatform = get(config, 'toPlatform', [])
    this.msg = get(config, 'msg', new ArrayBuffer(0))
    this.ttl = get(config, 'ttl', 0)
  }

  Decode: (buffer: ArrayBuffer) => {
    toAppid: string;
    toUid: string[];
    toPlatform: number[];
    msg: ArrayBuffer;
    ttl: number;
  } = buffer => {
    const bk = new ReadBlock(buffer)
    const toAppid = bk.ReadStringField()
    const toUid = []
    const toPlatform = []
    const size1 = bk.ReadInt32Field()
    for (let i = 0; i < size1; i++) {
      const str = bk.ReadStringField()
      toUid.push(str)
    }
    const size2 = bk.ReadInt32Field()
    for (let i = 0; i < size2; i++) {
      const nVal = bk.ReadInt16Field()
      toPlatform.push(nVal)
    }
    const msg = bk.ReadArrayBufferField()
    const ttl = bk.ReadInt32Field()
    return {
      toAppid,
      toUid,
      toPlatform,
      msg,
      ttl,
    }
  }

  Encode: () => ArrayBuffer = () => {
    const bk = new WriteBlock()
    bk.WriteStringField(this.toAppid)

    const size1 = this.toUid.length
    bk.WriteInt32Field(size1)
    for (let i = 0; i < size1; i++) {
      bk.WriteStringField(this.toUid[i])
    }
    const size2 = this.toPlatform.length
    bk.WriteInt32Field(size2)
    for (let i = 0; i < size2; i++) {
      bk.WriteInt16Field(this.toPlatform[i])
    }
    bk.WriteArrayBufferField(this.msg)
    bk.WriteInt32Field(this.ttl)
    return bk.getArrayBuffer()
  }
}

class UidInfo {
  uid: string;
  platform: number;

  constructor(config) {
    this.uid = get(config, 'uid', '')
    this.platform = get(config, 'platform', 0)
  }
}


class SingleCmdRsp {
  code: number;
  message: string;
  traceId: string;
  offlineUidInfos: Array<UidInfo>;

  constructor(config?) {
    this.code = get(config, 'code', 0)
    this.message = get(config, 'message', '')
    this.traceId = get(config, 'traceId', '')
    this.offlineUidInfos = get(config, 'offlineUidInfos', [])
  }

  Decode: (buffer: ArrayBuffer) => {
    code: number;
    message: string;
    traceId: string;
    offlineUidInfos: UidInfo[];
  } = buffer => {
    const bk = new ReadBlock(buffer)

    const code = bk.ReadInt32Field()
    const message = bk.ReadStringField()
    const traceId = bk.ReadStringField()
    const offlineUidInfos = []

    const size = bk.ReadInt32Field()
    for (let i = 0; i < size; i++) {
      const info = new UidInfo({
        uid: bk.ReadStringField(),
        platform: bk.ReadInt16Field(),
      })

      offlineUidInfos.push(info)
    }

    return {
      code,
      message,
      traceId,
      offlineUidInfos,
    }
  }

  Encode: () => ArrayBuffer = () => {
    const bk = new WriteBlock()
    bk.WriteInt32Field(this.code)
    bk.WriteStringField(this.message)
    bk.WriteStringField(this.traceId)
    const size = this.offlineUidInfos.length
    bk.WriteInt32Field(size)
    for (let i = 0; i < size; i++) {
      const info = this.offlineUidInfos[i]
      bk.WriteStringField(info.uid)
      bk.WriteInt16Field(info.platform)
    }
    return bk.getArrayBuffer()
  }
}

