export enum CMD_CODE {
  CONNECT = 1,
  CONNECTACK = 2,
  DISCONNECT = 3,
  DISCONNECTACK = 4,
  UPDATE = 5,  //  动态修改心跳，现在还未支持，智能心跳；
  UPDATEACK = 6,
  BIND = 7, //  登录账号（推送可能用到）
  BINDACK = 8,  //
  UNBIND = 9, //  login out
  UNBINDACK = 10,
  PING = 11, //  心跳 （如果不发心跳的话，服务器会断开，setTimeout 2000）
  PONG = 12, //  本地也需要做心跳判断；心跳时间 * 2
  PUSH = 13, //  发消息
  PUSHACK = 14,
  CALL = 15,
  CALLACK = 16,
}

export enum PLATFORM_TYPE
{
  Unknow = 0,
  Win = 1,
  Android = 2,
  iOS = 3,
  Web = 4,
  SeewoOS = 5,
  Mac = 6,
  Linux = 7,
}


export interface RtmqPack {
  cmd: CMD_CODE;// 指令字
  seq: number; // 传输序列
  data: number; // 传输数据
  extend?: number; // 扩展
}

// 连接命令
export interface Connect
{
  traceid: string; // 消息ID,客户生成UUID则可
  appid: string; // 应用ID
  cid: string; // 客户端ID
  uid: string; // 账号ID
  pwd: string; // 账号密码
  data: ArrayBuffer; // 认证数据
  will: ArrayBuffer; // 断开遗愿
  signsure: string; // 签名
  version: string; // 网络库版本
  platform: PLATFORM_TYPE; // 平台类型
  heartbeat: number; // 心跳，单位为秒
}

export interface ConnectAck
{
  traceid: string;  // 透传回的消息ID
  appid: string;  // 应用ID
  cid: string;  // 后台分配的客户ID
  uid: string;  // 账号ID
  token: string;  // 用于web访问的认证token
  data: ArrayBuffer;  // 认证返回的数据
  code: number;  // 错误码，0是成功，其他为失败
  message: string;  // 错误码描述
}


// 断开命令
export interface Disconnect
{
  traceid: string; // 透传回的消息ID
  will: ArrayBuffer; // 遗愿，如果不为空会覆盖默认的遗愿
  data?: ArrayBuffer;
}

export interface DisconnectAck
{
  traceid: string; // 断开遗愿
  code: number; // 错误码，0是成功，其他为失败
  message: string; // 错误码描述
  data?: ArrayBuffer;
}

// 绑定命令
export interface Bind
{
  traceid: string; // 消息ID,客户生成UUID则可
  appid: string; // 应用ID,默认填空
  cid: string; // 客户端ID
  uid: string; // 绑定的账号ID
  pwd: string; // 账号密码
  data: ArrayBuffer; // 认证数据
  will: ArrayBuffer; // 断开时，默认使用的遗愿
}

export interface BindAck
{
  traceid: string; // 透传回的消息ID
  appid: string; // 应用ID,默认为空
  cid: string; // 客户端ID，暂时不使用
  uid: string; // 绑定的账号ID
  data: ArrayBuffer; // 认证返回的数据
  code: number; // 错误码，0是成功，其他为失败
  message: string; // 错误码描述
}

// 解绑命
export interface Unbind
{
  traceid: string; // 消息ID,客户生成UUID则可
  appid: string; // 应用ID
  cid: string; // 客户端ID
  uid: string; // 账号ID
  will: ArrayBuffer; // 遗愿，如果不为空会覆盖默认的遗愿
  data?: ArrayBuffer;
}

export interface UnbindAck
{
  traceid: string;// 透传回的消息ID
  appid: string;// 应用ID，默认留空
  cid: string;// 客户端ID，默认留空
  uid: string;// 账号ID
  code: number;// 错误码，0是成功，其他为失败
  message: string;// 错误码描述
  data?: ArrayBuffer;
}

// 推送命令
export interface Push
{
  traceid: string;// 消息ID,客户生成UUID则可
  topic: string;// 消息主题
  ack: boolean;// 是否应答
  data: ArrayBuffer;// 推送的数据
  extend: ArrayBuffer;// 扩展字段
}

export interface PushAck
{
  traceid: string;// 透传回的消息ID
  topic: string;// 透传回的主题
  extend: ArrayBuffer;// 扩展字段
  code: number;// 错误码，0是成功，其他为失败
  message: string;// 错误码描述
  data?: ArrayBuffer;
}

// 调用命令
export interface Call
{
  traceid: string;// 消息ID,客户生成UUID则可
  topic: string;// 消息主题
  data: ArrayBuffer;// 推送的数据
  extend?: ArrayBuffer;// 扩展字段
}

export interface CallAck
{
  traceid: string;// 透传回的消息ID
  topic: string;// 透传回的主题
  data: ArrayBuffer;// 调用响应数据
  extend: ArrayBuffer;// 透传扩展字段
  code: number;// 错误码，0是成功，其他为失败
  message: string;// 错误码描述
}
