import {
  json2RtmqBuf,
  rtmqBuf2Json,
} from '../../common/protobuf/protoUtil'
import {
  TIMEOUT,
  DEFAULT_HEARTBEAT,
} from '../../common/constant'
import {
  closeType,
  closeReason,
  connectReason,
  connectType,
} from './responseDataSet'
import {
  log,
} from '../../common/console'
import {
  AnyJson,
} from '../../common/types/core'
import Event from 'events'
import exceptionHandle from './exceptionHandle'
import AckRequest from './AckRequest'
import uuidv4 from 'uuid/v4'
import version from '../../common/version'
import axios from 'axios'
import get from 'lodash/get'
import {
  CMD_CODE,
  PLATFORM_TYPE,
  Connect,
  ConnectAck,
  Disconnect,
  DisconnectAck,
  Bind,
  BindAck,
  Unbind,
  UnbindAck,
  Push,
  PushAck,
  Call,
  CallAck,
} from '../../common/types/rtmq'
import cloneDeep from 'lodash/cloneDeep'


type RtmqData = Connect | Disconnect | Bind | Unbind | Push | Call | ConnectAck | DisconnectAck | BindAck | UnbindAck | PushAck | CallAck

type ParseRtmqPack = {
  cmd: CMD_CODE;
  seq: number;
  data: RtmqData;
}

interface ConnectCbData {
  code: number;
  message: string;
}

interface ConnectCb {
  (data: ConnectCbData): any;
}

interface InitInfo {
  host: string;
  appid: string;
  uid?: string;
  pwd?: string;
  platform: number;
  data: ArrayBuffer;
  heartbeat?: number;
  will?: ArrayBuffer;
  isAutoReconnect?: boolean;
  cid?: string; // TODO: 是不是要删除？
}

interface SendCbData {
  traceid: string;
  topic: string;
  code: number;
  message: string;
  extend: ArrayBuffer;
}

interface SendRequestCb {
  (data: SendCbData): any;
}

export interface CallRequest {
  rtmqData: RtmqData;
  onSuccess?: CallRequestCb;
  onFail?: CallRequestCb;
  onTimeout?: Function;
}

export interface SendRequest {
  rtmqData: RtmqData;
  onSuccess?: SendRequestCb;
  onFail?: SendRequestCb;
  onTimeout?: Function;
}

interface CallCbData {
  traceid: string;
  data: ArrayBuffer;
  topic: string;
  code: number;
  message: string;
  extend: ArrayBuffer;
}

interface CallRequestCb {
  (data: CallCbData): any;
}

interface ReadParser {
  id: string;
  bin: ArrayBuffer;
  json: AnyJson;
  done: boolean;
  cb: Function;
}

// 这里的data能是string吗？
interface CommonCoreInfo {
  host?: string;
  appid?: string;
  cid?: string;
  uid?: string;
  pwd?: string;
  platform?: number;
  data?: ArrayBuffer;
  heartbeat?: number;
  will?: string;
  isAutoReconnect?: boolean;
  token: string;
  traceid: string;
}

interface TrueInitInfo {
  host: string;
  appid: string;
  cid: string;
  uid: string;
  pwd: string;
  platform: PLATFORM_TYPE;
  data: ArrayBuffer;
  heartbeat: number;
  version: string;
  signsure: string;
  will: ArrayBuffer;
}


interface RecvData {
  ack: boolean;
  data: ArrayBuffer;
  extend: ArrayBuffer;
  topic: string;
  traceid: string;
}

interface RecvFunction {
  (data: RecvData): any;
}

export class NetBase {
  initInfo: TrueInitInfo;
  commonCoreInfo: CommonCoreInfo;
  websocket: WebSocket;
  isAutoReconnect: boolean;
  _rtmqEvent: Event;
  _seq: number;
  _timeout: number;
  _ackRequests: Map<number, AckRequest>;
  _isConnected: boolean;
  _isUserClose: boolean;
  _closeReason: ConnectCbData;
  _connectReason: ConnectCbData;
  _isReconnecting: boolean;
  _recvQueue: Array<ReadParser>;
  cid: string;

  constructor() {
    this.initInfo = null
    this.commonCoreInfo = null
    this._rtmqEvent = new Event()
    this._seq = 0 // 不可修改
    this._timeout = TIMEOUT // 超时时间,单位秒
    this._ackRequests = new Map() // 根据seq存储的请求对象列表，响应后释放
    this.isAutoReconnect = true // 断开后是否自动重连
    this._isConnected = false // 是否连上
    this._isUserClose = false // 是否是开发者发起的断开连接
    this._closeReason = closeReason[closeType.UNKNOW]() // 断开连接原因
    this._connectReason = null // 连接成功原因
    this._isReconnecting = false // 是否正在重连
    this._recvQueue = []// 消息接收队列
  }

  /**
   * 初始化
   * @param {object} initInfo 初始化数据
   */
  init(initInfo: InitInfo): this {
    this.initInfo = {
      host: initInfo.host,
      appid: initInfo.appid.trim(),
      cid: initInfo.cid, // TODO:这里要填什么？忘了
      uid: initInfo.uid,
      pwd: initInfo.pwd,
      platform: initInfo.platform,
      data: initInfo.data,
      heartbeat: initInfo.heartbeat || DEFAULT_HEARTBEAT,
      version: version,
      signsure: '7db047a67a9d7293850ac69d14cc82bf',
      will: initInfo.will,
    }

    this.commonCoreInfo = Object.assign({} as CommonCoreInfo, this.initInfo)
    this.isAutoReconnect = initInfo.isAutoReconnect === false ? false : true

    return this
  }

  /**
   * 创建ws连接后台，连接成功后执行了部分操作：
   * 1.发送CONNECT请求以执行合法操作
   * 2.心跳包自动发送
   * 3.接收后台数据并触发相应事件
   * 4.关闭连接的后续处理
   * @param {object} listenObj 包含监听连接操作的函数， 接收onSuccess、onFailure、onTimeout方法
   */
  start(): this {
    if (this.websocket) {
      throw new Error('已存在websocket连接')
    }
    const {
      host,
      appid,
    } = this.initInfo

    const straight: boolean = /(^wss:\/\/)|(^ws:\/\/)/.test(host)
    const conn = (url) => {
      this.websocket = new WebSocket(url)
      this._isUserClose = false

      // 当打开的时候调用
      this.websocket.onopen = () => {
        this.websocket.binaryType = 'arraybuffer'

        const info: Connect = Object.assign({}, this.initInfo, {
          traceid: uuidv4(),
        })

        log('_MSSDK_NetBase_ start onopen CONNECT _send info:', info)
        this._send(CMD_CODE.CONNECT, info).then((rtmqData: RtmqData) => {
          log('_MSSDK_NetBase_ start onopen CONNECT then rtmqData:', rtmqData)
          this._isReconnecting = false
        }).onFail((rtmqData: RtmqData) => {
          log('_MSSDK_NetBase_ start onopen CONNECT onFail rtmqData:', rtmqData)
          this._connectFailDeal(rtmqData as ConnectAck | DisconnectAck | BindAck | UnbindAck | PushAck | CallAck)
        }).onTimeout((rtmqData: RtmqData) => {
          log('_MSSDK_NetBase_ start onopen CONNECT onTimeout:', rtmqData)
          this._close(closeType.CONNECT_TIMEOUT)
        })
      }

      this.websocket.onerror = (evt) => {
        log('_MSSDK_NetBase_ start onerror event:', evt)
        this._close(closeType.WEBSOCKET_ERROR)
      }

      // 当接收消息的时候
      this.websocket.onmessage = (evt) => {
        // let rtmqPack = new RTMQ.RtmqPack().Decode(evt.data)

        // this._readRecvData(evt.data, (rtmqPack) => {
        // rtmqPack && rtmqPack.cid && (self.cid = rtmqPack.cid); //TODO:这个是否需要？
        const rtmqPack = rtmqBuf2Json(evt.data)

        Object.keys(CMD_CODE).map((code) => {
          if (CMD_CODE[code] === rtmqPack.cmd) {
            this._recvPreDeal(code, rtmqPack.data, { seq: rtmqPack.seq})
          }
        })

        this._handleRequestResponse(rtmqPack)
        // this._updateCommonCoreInfo(rtmqPack);
        // })
      }

      // 当关闭 websocket 的时候
      this.websocket.onclose = (evt) => {
        this._isConnected = false
        if (this._isReconnecting) {
          this._closeReason = closeReason[closeType.RECONNECT_FAIL]()
        }
        this._rtmqEvent.emit('disconnect')
        this.websocket = null
        if (this._isUserClose) {
          this._rtmqEvent.removeAllListeners() // 移除所有事件监听
        }
        this._reconnect()
      }
    }
    let cnurl
    if(!straight) {
      axios.get(`https://${host}/gateway/api/v1/getroute/default/${appid}?protocol=wss`).then((res) => {
        const port = get(res, 'data.port')
        const ip = get(res, 'data.ip')
        const protocol = get(res, 'data.protocol')
        cnurl = `${protocol}://${ip}:${port}`
        conn(cnurl)
      }).catch((e) => {
        console.error(`[MsSDK]get websocket url error, ${e}`)
        cnurl = host
        conn(`wss://${host}`)
      })
    }else {
      cnurl = host
      conn(cnurl)
    }

    return this
  }

  /**
   * 断开ws连接
   * @param {object} listenObj 包含监听连接操作的函数， 接收onSuccess方法
   */
  stop(will?: ArrayBuffer): this {
    this._isUserClose = true

    if (this.websocket) {
      let timer = null

      const actualClose = () => {
        this._close(closeType.USER_CLOSE)
        clearTimeout(timer)
      }

      // 超时则强行断开连接
      timer = setTimeout(() => {
        actualClose()
      }, this._timeout * 1000)

      const params: Disconnect = Object.assign({}, this.commonCoreInfo, will ? {
        will,
      } : null)

      this._send(CMD_CODE.DISCONNECT, params)
      this._rtmqEvent.on('DISCONNECTACK', () => {
        actualClose()
      })
    }
    return this
  }


  /**
   * 发送PUSH类型数据
   * @param {object} rtmqData rtmq协议CALL类型对应的data
   */
  send({
    rtmqData,
    onSuccess,
    onFail,
    onTimeout,
  }: SendRequest): this {
    log('_MSSDK_NetBase_ send rtmqData:', rtmqData)
    this._send(CMD_CODE.PUSH, rtmqData).then((resRtmqData: PushAck) => {
      log('_MSSDK_NetBase_ send then resRtmqData:', resRtmqData)
      onSuccess && onSuccess(resRtmqData)
    }).onFail((resRtmqData: PushAck) => {
      log('_MSSDK_NetBase_ send onFail resRtmqData:', resRtmqData)
      onFail && onFail(resRtmqData)
    }).onTimeout((rtmqData: Push) => {
      log('_MSSDK_NetBase_ send onTimeout:', rtmqData)
      onTimeout && onTimeout(rtmqData)
    })
    return this
  }

  /**
   * 发送CALL类型数据，这种类型会要求服务端返回有效数据
   * @param {object} rtmqData rtmq协议CALL类型对应的data
   */
  call({
    rtmqData,
    onSuccess,
    onFail,
    onTimeout,
  }: CallRequest): this {
    log('_MSSDK_NetBase_ call rtmqData:', rtmqData)
    this._send(CMD_CODE.CALL, rtmqData).then((resRtmqData: CallAck) => {
      log('_MSSDK_NetBase_ call then resRtmqData:', resRtmqData)
      onSuccess && onSuccess(resRtmqData)
    }).onFail((resRtmqData: CallAck) => {
      log('_MSSDK_NetBase_ call onFail resRtmqData:', resRtmqData)
      onFail && onFail(resRtmqData)
    }).onTimeout((rtmqData: Call) => {
      log('_MSSDK_NetBase_ call onTimeout:', rtmqData)
      onTimeout && onTimeout(rtmqData)
    })
    return this
  }

  /**
   * 监听获取消息事件，仅包括：PUSH类型
   * @param {function} fn 回调函数
   */
  onRecv(fn: RecvFunction): this {
    this._rtmqEvent.on('PUSH', (rtmqData, context) => {
      log('_MSSDK_NetBase_ onRecv rtmqData:', rtmqData)
      // 深拷贝数据抛出，防止同一事件中数据被篡改引发bug
      const cloneRtmqData = cloneDeep(rtmqData)
      fn && fn(cloneRtmqData)
    })
    return this
  }

  /**
   * 握手成功进行回调
   */
  onConnect(cb: ConnectCb) {
    this._rtmqEvent.on('CONNECTACK', (rtmqData) => {
      if (rtmqData.code === 0) {
        this._isConnected = true
        exceptionHandle(this)
        if (!this._connectReason) {
          this._connectReason = connectReason[connectType.USER_CONNECT]()
        }
        log('_MSSDK_NetBase_ onConnect')
        cb && cb(this._connectReason)
      }
      this._connectReason = null
    })
    return this
  }

  /**
   * 断开连接后进行回调
   */
  onDisconnect(cb: ConnectCb): this {
    this._rtmqEvent.on('disconnect', () => {
      log('_MSSDK_NetBase_ onDisconnect')
      cb && cb(this._closeReason)
    })
    return this
  }

  /**
   * 获取连接状态
   */
  getIsConnected(): boolean {
    return this._isConnected
  }

  _start(type: string, customData?: AnyJson): void {
    this._connectReason = connectReason[type](customData)
    this.start()
  }

  _close(type: string, customData?: AnyJson): void {
    this._closeReason = closeReason[type](customData)
    // 心跳异常的情况，大概率是网络断开，此处自己触发断开回调函数，执行重连操作
    if(type === closeType.HEARTBEAT_FAIL) {
      const originWebsocket = this.websocket
      this.websocket.close()
      this.websocket.onclose(null)
      // eslint-disable-next-line @typescript-eslint/no-empty-function
      originWebsocket.onclose = function() {}
    }else{
      this.websocket && this.websocket.close()
    }
  }

  _connectFailDeal(rtmqData: ConnectAck | DisconnectAck | BindAck | UnbindAck | PushAck | CallAck): void {
    this._isUserClose = true
    this._close(closeType.CONNECT_FAIL, {
      code: rtmqData.code,
      message: rtmqData.message,
      traceid: rtmqData.traceid,
    })
  }

  _recvPreDeal(code, rtmqData, context) {
    // 接收消息的单次处理！不能放在事件监听函数里面，如果放在onPush监听函数里面，上层监听两次则此处会执行两次，是不对的。
    if(code + '' === 'PUSH') {
      const cloneRtmqData = cloneDeep(rtmqData)
      if(cloneRtmqData.ack) {
        this._send(CMD_CODE.PUSHACK, {
          traceid: cloneRtmqData.traceid,
          topic: cloneRtmqData.topic,
          extend: cloneRtmqData.extend,
          code: 0,
          message: '',
        }, context.seq)
      }
    }

    this._rtmqEvent.emit(code, rtmqData, context)
  }

  /**
   * 重新连接逻辑
   */
  _reconnect() {
    if (!this._isUserClose && this.isAutoReconnect) {
      const reconnect = () => {
        this._isReconnecting = true
        log('_MSSDK_NetBase_ 自动重连中')
        setTimeout(() => {
          if(!this._isUserClose) {
            this._start(connectType.RECONNECT)
          }
        }, 10 * 1000)
      }

      reconnect()
    }
  }

  /**
   * 收到信息时触发对应请求的响应
   * @param {object} rtmqPack rtmq协议包数据
   */
  _handleRequestResponse(rtmqPack: ParseRtmqPack) {
    const request = this._ackRequests.get(rtmqPack.seq)
    rtmqPack.data
      && request
      && request.emit(((rtmqPack.data) as ConnectAck | DisconnectAck | BindAck | UnbindAck | PushAck | CallAck).code === 0
        ? 'success' : 'fail', rtmqPack.data)
    this._ackRequests.delete(rtmqPack.seq)
  }

  /**
   * 根据cmd code发送对应的rtmqData
   * @param {number} cmdCode
   * @param {object} rtmqData rtmq协议的data
   * @param {number} cusSeq 自定义seq值
   */
  _send(cmdCode: CMD_CODE, rtmqData: RtmqData, cusSeq?: number) {
    const request = new AckRequest(() => {
      log('_MSSDK_NetBase_ _send rtmqData:', cmdCode, rtmqData)
      const binRtmqPack = json2RtmqBuf(cmdCode, cusSeq || this._seq, rtmqData)
      this.websocket && this.websocket.readyState === 1 && this.websocket.send(binRtmqPack)
    }, rtmqData)
    request.onTimeout(() => {
      this._ackRequests.delete(this._seq)
      if (!this.websocket) {
        console.error('websocket未连接')
      }
    })
    this._ackRequests.set(this._seq, request)
    this._seq++
    return request
  }
}
