  /**
   * 创建IM实例本身，初始化基本数据。在退出账号使用另一个账号的时候可以再次调用修改基本数据，此时未建立websocket连接。
   * @param {InitInfo} initInfo 基本数据
   */
  public init (initInfo: InitInfo) {
    const newInitInfo = {
      host: initInfo.host,
      appid: initInfo.appid,
      uid: initInfo.uid,
      pwd: initInfo.pwd,
      platform: initInfo.platform,
      heartbeat: initInfo.heartbeat,
      isAutoReconnect: initInfo.isAutoReconnect,
      authInfo: {
        auth: initInfo.auth,
      },
      will: initInfo.will,
    }

    this.controller.init(newInitInfo)
    this.isInit = true
    return this
  }

