  /**
   * 递归拉取单聊数据
   * @param {string} uid 要拉取的目标uid
   * @param {number} unread 未读消息数
   * @param {array} msg_ids 已经收到的消息id数组，用于给服务端确认已经收到了这些消息，可以从缓存中删除
   * @param {function} cb 每条消息的回调函数
   * @param {string} traceid 透传的traceid，非必填
   */
  private _pullSingleMessageLoop (uid, unread, msg_ids, cb, traceid?) {
    log('_MSSDK_IM_ _pullSingleMessageLoop arguments:', uid, unread, msg_ids)

    this._pullSingleMessage({
      data: {
        traceid: traceid,
        uid: uid,
        seq_id: this._getSeqMap(uid),
        size: unread,
        msg_id: msg_ids,
      },
      onSuccess: (apiData, context) => {
        log('_MSSDK_IM_ _pullSingleMessage onSuccess:', apiData, context)
        const messages = apiData.data
        messages.length && this._saveSeqMap(apiData.uid, messages[messages.length - 1].seq_id)

        if (apiData.unread === 0) {
          this._pullSingleMessage({
            data: {
              traceid: context.traceid,
              uid: apiData.uid,
              seq_id: this._getSeqMap(apiData.uid),
              size: 0,
              msg_id: messages.map(msg => msg.msg_id),
            },
          })
        } else if (apiData.unread > 0) {
          this._pullSingleMessageLoop(apiData.uid, apiData.unread, apiData.data.map((message) => message.msg_id), cb, context.traceid)
        }
        messages.map((message) => {
          cb && cb(message, context)
        })
      },
      onFail: (apiData, context) => {
        log('_MSSDK_IM_ _pullSingleMessage onFail:', apiData, context)
      },
      onTimeout: (apiData, context) => {
        log('_MSSDK_IM_ _pullSingleMessage onTimeout:', apiData, context)
      },
    })
  }

  /**
   * 递归拉取群聊数据
   * @param {string} gid 需要拉取的群ID
   * @param {number} unread 未读消息数
   * @param {function} cb 每条消息的回调函数
   * @param {string} traceid 透传的traceid，非必填
   */
  private _pullGroupMessageLoop (gid, unread, cb, traceid?) {
    log('_MSSDK_IM_ _pullGroupMessageLoop arguments:', gid, unread)

    this._pullGroupMessage({
      data: {
        traceid: traceid,
        gid: gid,
        seq_id: this._getSeqMap(gid),
        size: unread,
      },
      onSuccess: (apiData, context) => {
        log('_MSSDK_IM_ _pullGroupMessage onSuccess:', apiData, context)
        const messages = apiData.data
        messages.length && this._saveSeqMap(apiData.gid, messages[messages.length - 1].seq_id)

        if (apiData.unread === 0) {
          this._pullGroupMessage({
            data: {
              traceid: context.traceid,
              gid: apiData.gid,
              seq_id: this._getSeqMap(apiData.gid),
              size: 0,
            },
          })
        } else if (apiData.unread > 0) {
          this._pullGroupMessageLoop(apiData.gid, apiData.unread, cb, context.traceid)
        }
        messages.map((message) => {
          cb && cb(message, context)
        })
      },
      onFail: (apiData, context) => {
        log('_MSSDK_IM_ _pullGroupMessage onFail:', apiData, context)
      },
      onTimeout: (apiData, context) => {
        log('_MSSDK_IM_ _pullGroupMessage onTimeout:', apiData, context)
      },
    })
  }

  public _pullGroupMessage (req: ApplyRequest) {
    const req2: ControllerRequest = createCallReq({ method: 'pullGroupMessage', data: req })
    this.controller.call(req2)
  }

  private _pullRoomMessageLoop (roomid, unread, cb, traceid?) {
    log('_MSSDK_IM_ _pullRoomMessageLoop arguments:', roomid, unread)

    this._pullRoomMessage({
      data: {
        traceid: traceid,
        roomid: roomid,
        seq_id: this._getSeqMap(roomid),
        size: unread,
      },
      onSuccess: (apiData, context) => {
        log('_MSSDK_IM_ _pullGroupMessage onSuccess:', apiData, context)
        const messages = apiData.data
        messages.length && this._saveSeqMap(apiData.roomid, messages[messages.length - 1].seq_id)

        if (apiData.unread === 0) {
          this._pullRoomMessage({
            data: {
              traceid: context.traceid,
              roomid: apiData.roomid,
              seq_id: this._getSeqMap(apiData.roomid),
              size: 0,
            },
          })
        } else if (apiData.unread > 0) {
          this._pullRoomMessageLoop(apiData.roomid, apiData.unread, cb, context.traceid)
        }
        messages.map((message) => {
          cb && cb(message, context)
        })
      },
      onFail: (apiData, context) => {
        log('_MSSDK_IM_ _pullGroupMessage onFail:', apiData, context)
      },
      onTimeout: (apiData, context) => {
        log('_MSSDK_IM_ _pullGroupMessage onTimeout:', apiData, context)
      },
    })
  }
