  /**
   * 收到主动推送的消息时, 保存对应消息来源的seq_id
   */
  // TODO: 返回设置结果： boolean
  public _saveSeqMap (key, seqId): boolean {
    const cacheSeq = this._seqMap.get('' + key)
    let res = false
    if (!cacheSeq) {
      this._seqMap.set('' + key, seqId)
      res = true
    } else {
      if (seqId > cacheSeq) {
        this._seqMap.set('' + key, seqId)
        res = true
      }
    }
    return res
  }

  public _getSeqMap (key) {
    return this._seqMap.get('' + key) || 0
  }


  /**
   * 发送IM消息
   * @param {string} chatType 发送IM的消息类型：['singleChat', 'groupChat']
   * @param {object} data singleChat API需要的数据
   */
  private _chat (chatType: string, req: ApplyRequest) {
    enum TargetType {
      singleChat = 1,
      groupChat = 2,
      roomChat = 3,
    }
    const msgbody = get(req, 'data.msg_body', '')
    if (sizeof(JSON.stringify(msgbody)) > 10240) {
      const onFail = get(req, 'onFail')
      return onFail && onFail({
        code: -3,
        message: '内容过多，超过限制',
      })
    }

    const req2: ControllerRequest = createCallReq({
      method: chatType,
      data: {
        ...req,
        data: {
          ...req.data,
          target_type: TargetType[chatType],
        },
      },
    })
    this.controller.call(req2)
  }

