import * as RTMQ from '../rtmqProtocol'
import * as CMD from '../cmdProtocol'
import { AnyJson } from '../../common/types/core'
import {
  CMD_CODE,
  Connect,
  ConnectAck,
  Disconnect,
  DisconnectAck,
  Bind,
  BindAck,
  Unbind,
  UnbindAck,
  Push,
  PushAck,
  Call,
  CallAck,
} from '../../common/types/rtmq'
import {
  ImData,
} from '../../common/types/apply'

type RtmqData = Connect | Disconnect | Bind | Unbind | Push | Call | ConnectAck | DisconnectAck | BindAck | UnbindAck | PushAck | CallAck

type ParseRtmqPack = {
  cmd: CMD_CODE;
  seq: number;
  data: RtmqData;
}

interface ApiPackAck {
  method: string;
  data: ImData;
}
/**
 * TODO: 单元测试
 * rtmqBuf数据转成json
 * @param {*} rtmqBuf
 */
export const rtmqBuf2Json: (rtmqAb: ArrayBuffer) => ParseRtmqPack = (rtmqAb) => {
  const rtmqPack = new RTMQ.RtmqPack().Decode(rtmqAb)

  const newRtmqPack: ParseRtmqPack = {
    cmd: rtmqPack.cmd,
    seq: rtmqPack.seq,
    data: undefined,
  }

  switch (rtmqPack.cmd) {
    case CMD_CODE.CONNECTACK:
      newRtmqPack.data = (new RTMQ.ConnectRsp().Decode(rtmqPack.data))
      break
    case CMD_CODE.DISCONNECTACK:
      newRtmqPack.data = (new RTMQ.DisconnectRsp().Decode(rtmqPack.data))
      break
    case CMD_CODE.UPDATEACK:
      break
    case CMD_CODE.BINDACK:
      newRtmqPack.data = (new RTMQ.BindRsp().Decode(rtmqPack.data))
      break
    case CMD_CODE.UNBINDACK:
      newRtmqPack.data = (new RTMQ.UnbindRsp().Decode(rtmqPack.data))
      break
    case CMD_CODE.PONG:
      newRtmqPack.data = (new RTMQ.Pong().Decode())
      break
    case CMD_CODE.PUSH:
      newRtmqPack.data = (new RTMQ.PushReq().Decode(rtmqPack.data))
      break
    case CMD_CODE.PUSHACK:
      newRtmqPack.data = (new RTMQ.PushRsp().Decode(rtmqPack.data))
      break
    case CMD_CODE.CALLACK:
      newRtmqPack.data = (new RTMQ.CallRsp().Decode(rtmqPack.data))
      break
    default:
      throw new Error('[MsSDK]cmd code is not match')
  }
  return newRtmqPack

}

// 处理发送消息的时候序列化处理
// TODO 单元测试
export const json2RtmqBuf: (type: CMD_CODE, seq: number, content: RtmqData) => ArrayBuffer = (type, seq, content) => {
  let result: ArrayBuffer = null
  switch (type) {
    case CMD_CODE.CONNECT:
      result = new RTMQ.ConnectReq(content as Connect).Encode(); break
    case CMD_CODE.DISCONNECT:
      result = new RTMQ.DisconnectReq(content as Disconnect).Encode(); break
    // case CMD_CODE.UPDATE: // TODO:
    //   result = new RTMQ.ConnectReq(content).Encode(); break;
    case CMD_CODE.BIND:
      result = new RTMQ.BindReq(content as Bind).Encode(); break
    case CMD_CODE.UNBIND:
      result = new RTMQ.UnbindReq(content as Unbind).Encode(); break
    case CMD_CODE.PING: // TODO:
      result = new RTMQ.Ping().Encode(); break
    case CMD_CODE.PUSH:
      result = new RTMQ.PushReq(content as Push).Encode(); break
    case CMD_CODE.PUSHACK:  // TODO:
      result = new RTMQ.PushRsp(content as PushAck).Encode(); break
    case CMD_CODE.CALL:
      result = new RTMQ.CallReq(content as Call).Encode(); break
    default:
      throw new Error('[MsSDK]type is incorrect.')
  }

  const rtmqPack = new RTMQ.RtmqPack({
    seq,
    cmd: type,
    data: result,
  })

  return rtmqPack.Encode()
}

export const getCmdBinArr: (method: string, json: AnyJson) => ArrayBuffer = (method, json) => {
  let req = null
  switch (method) {
    case 'singleCmd':
      req = new CMD.SingleCmdReq({
        toApp: json.toApp,
        toUid: json.toUid,
        toPlatform: json.toPlatform,
        msg: json.msg,
        ttl: json.ttl,
      })
      break
    case 'roomCmd':
      req = new CMD.RoomCmdReq({
        roomid: json.roomid,
        ttl: json.ttl,
        msg: json.msg,
      })
      break
    case 'pullHisRoomCmd':
      req = new CMD.PullHisRoomCmdReq({
        roomid: json.roomid,
        seqid: json.seqid,
        size: json.size,
      })
      break
    default:
      throw new Error('[MsSDK]method is not match.')
  }

  const cmdPack = new CMD.CommandPack({
    method,
    data: req.Encode(),
  })

  const postData = cmdPack.Encode()

  return postData
}

export const cmdBuf2Json: (cmdBuf: ArrayBuffer) => ApiPackAck = cmdBuf => {
  const cmdPack = new CMD.CommandPack().Decode(cmdBuf)
  let data

  switch (cmdPack.method) {
    case 'singleCmdRsp':
      data = (new CMD.SingleCmdRsp().Decode(cmdPack.data))
      break
    case 'roomCmdRsp':
      data = (new CMD.RoomCmdRsp().Decode(cmdPack.data))
      break
    case 'pullHisRoomCmdRsp':
      data = new CMD.PullHisRoomCmdRsp().Decode(cmdPack.data)
      break
    case 'singleCmdData':
      data = (new CMD.SingleCmdData().Decode(cmdPack.data))
      break
    case 'roomCmdData':
      data = new CMD.RoomCmdData().Decode(cmdPack.data)
      break
    default:
      throw new Error('[MsSDK]method is not match.')
  }

  return {
    method: cmdPack.method,
    data,
    extend: cmdPack.extend,
  }
}

