export const closeType = {
  USER_CLOSE: 'userClose',
  CONNECT_TIMEOUT: 'connectTimeout',
  CONNECT_FAIL: 'connectFail',
  RECONNECT_FAIL: 'reconnectFail',
  HEARTBEAT_FAIL: 'heartbeatFail',
  WEBSOCKET_ERROR: 'websocketError',
  UNKNOW: 'unknow',
}

export const connectType = {
  USER_CONNECT: 'userConnect',
  RECONNECT: 'reconnect',
}

export const closeReason = {
  userClose: () => ({
    code: 0,
    message: '断开连接',
  }),
  connectTimeout: () => ({
    code: -10000,
    message: '连接超时',
  }),
  connectFail: (data) => data,
  reconnectFail: () => ({
    code: -10001,
    message: '重新连接失败',
  }),
  heartbeatFail: () => ({
    code: -10002,
    message: '连接异常',
  }),
  websocketError: () => ({
    code: -10003,
    message: '连接websocket地址出错',
  }),
  unknow: () => ({
    code: -10004,
    message: '未知原因',
  }),
}

export const connectReason = {
  userConnect: () => ({
    code: 0,
    message: '成功连接',
  }),
  reconnect: () => ({
    code: 0,
    message: '重新连接成功',
  }),
}
