import { AnyJson } from '../common/types/core'
import StringView from '../module/stringview'
const str2ab = (str: string) => {
  const encoded = new StringView(str)
  return encoded.buffer
}
const ab2str = (buf) => {
  const sv = new StringView(buf)
  return sv.toString()
}

export const charCodes2Json: (charCodes: ArrayBuffer) => AnyJson = (charCodes) => {
  return JSON.parse(ab2str(charCodes))
}

export const json2ArrayBuffer: (json: AnyJson) => ArrayBuffer = json => {
  const str: string = JSON.stringify(json)
  const arr: ArrayBuffer = str2ab(str)
  return arr
}

export const str2ArrayBuffer: (str: string) => ArrayBuffer = str => {
  return str2ab(str)
}

export const binArr2Ab: (arr: number[]) => ArrayBuffer = (arr) => {
  return new Uint8Array(arr).buffer
}

/**
 * 计算一个字符串所占的字节数
 * @param {string} str 计算的字符串
 * @param {string} charset 计算字符串所占的内存字节数，默认使用'UTF-8'的编码方式计算，也可指定为'UTF-16'
 */
export const sizeof = function (str, charset?) {
  let total = 0,
    charCode,
    i,
    len
  charset = charset ? charset.toLowerCase() : ''
  if (charset === 'utf-16' || charset === 'utf16') {
    for (i = 0, len = str.length; i < len; i++) {
      charCode = str.charCodeAt(i)
      if (charCode <= 0xffff) {
        total += 2
      } else {
        total += 4
      }
    }
  } else {
    for (i = 0, len = str.length; i < len; i++) {
      charCode = str.charCodeAt(i)
      if (charCode <= 0x007f) {
        total += 1
      } else if (charCode <= 0x07ff) {
        total += 2
      } else if (charCode <= 0xffff) {
        total += 3
      } else {
        total += 4
      }
    }
  }
  return total
}

export const getQueryVariable = function (variable) {
  // RN环境没有window.location
  if (window.location) {
    const query = window.location.search.substring(1)
    const vars = query.split('&')
    for (let i = 0; i < vars.length; i++) {
      const pair = vars[i].split('=')
      if (pair[0] == variable) {
        return pair[1]
      }
    }
  }
  return (false)
}

/**
 * 获取一个值的类型
 * @param {*} value  值
 * @return {string}
 * e.g., getVariableType([]) => 'Array'
 */
export const getVariableType = (value) => {
  const toStringType = Object.prototype.toString.call(value)
  const type = toStringType.substring(8, toStringType.length - 1)
  return type
}

/**
 * 判断一个值是不是数组
 * @param {*} value 值
 * @return {boolean}
 * e.g., isArray([]) => true
 */
export const isArray = (value) => {
  return getVariableType(value) === 'Array'
}
