const ArrayToArrayBuffer: (arr: Array<number>) => ArrayBuffer = arr => {
  const bf = new ArrayBuffer(arr.length)
  const dv = new DataView(bf)
  for (let i = 0; i < arr.length; i++) {
    dv.setUint8(i, arr[i])
  }
  return bf
}

class ReadBlock {
  dv: DataView;
  pos: number;
  size: number;

  constructor(buffer: ArrayBuffer) {
    this.dv = new DataView(buffer)
    this.pos = 0
    this.size = buffer.byteLength
  }

  GetHead: (index: number, type: number) => boolean = (index, type) => {
    if (this.pos >= this.size) {
      return false
    }
    const head = this.dv.getUint8(this.pos)
    if ((head >> 3) != index || (head & 0x07) != type) {
      return false
    }
    this.pos++
    return true
  }

  GetIntValue: () => number = () => {
    let ret = 0
    let val = this.dv.getUint8(this.pos++)
    let step = 1
    while (val > 127) {
      val &= 0x7F
      ret += (val * step)
      step *= 128
      val = this.dv.getUint8(this.pos++)
    }
    ret += (val * step)
    return ret
  }

  ReadIntField: (index: number) => number = (index) => {
    if (!this.GetHead(index, 0)) {
      return 0
    }
    const val = this.GetIntValue()
    return val
  }

  ReadStringField: (index: number) => string = (index) => {
    let str = ''
    if (!this.GetHead(index, 2)) {
      return str
    }

    const len = this.GetIntValue()
    for (let i = 0; i < len; i++) {
      str += String.fromCharCode(this.dv.getUint8(this.pos++))
    }

    return str
  }

  ReadArrayField: (index: number) => ArrayBuffer = (index) => {
    const arr = []
    if (!this.GetHead(index, 2)) {
      return new Uint8Array(arr).buffer
    }

    const len = this.GetIntValue()
    for (let i = 0; i < len; i++) {
      arr.push(this.dv.getUint8(this.pos++))
    }
    return new Uint8Array(arr).buffer
  }
}

class WriteBlock {
  arr: Array<number>;

  constructor() {
    this.arr = []
  }

  SetHead: (index: number, type: number) => void = (index, type) => {
    const val = (index << 3) | type
    this.arr.push(val & 0xFF)
  }

  SetIntValue: (val: number) => void = (val) => {
    while (val > 127) {
      this.arr.push(val % 128 + 128)
      val = Math.floor(val / 128)
    }
    this.arr.push(val)
  }

  WriteIntField: (index: number, val: number) => void = (index, val) => {
    this.SetHead(index, 0)
    this.SetIntValue(val)
  }


  WriteBooleanField: (index: number, val: boolean) => void = (index, val) => {
    this.SetHead(index, 0)
    this.SetIntValue(+val)
  }

  WriteStringField: (index: number, str: string) => void = (index, str) => {
    this.SetHead(index, 2)
    this.SetIntValue(str.length)

    for (let i = 0; i < str.length; ++i) {
      this.arr.push(str.charCodeAt(i))
    }
  }

  WriteArrayBufferField: (index: number, buf: ArrayBuffer) => void = (index, buf) => {
    this.SetHead(index, 2)
    this.SetIntValue(buf.byteLength)
    const ui8 = new Uint8Array(buf)
    for (let i = 0; i < ui8.length; i++) {
      this.arr.push(ui8[i])
    }
  }

  getArrayBuffer: () => ArrayBuffer = () => {
    return new Uint8Array(this.arr).buffer
  }
}

// rtmq pack
class RtmqPack {
  cmd: number;
  seq: number;
  data: ArrayBuffer;

  constructor(config?) {
    this.cmd = get(config, 'cmd', 0)
    this.seq = get(config, 'seq', 0)
    this.data = get(config, 'data', [])
  }

  // input arraybuffer
  Decode: (buffer: ArrayBuffer) => {
    cmd: number;
    seq: number;
    data: ArrayBuffer;
  } = (buffer) => {
    const bk = new ReadBlock(buffer)
    // this.cmd = bk.ReadIntField(1);
    // this.seq = bk.ReadIntField(2);
    // this.data = bk.ReadArrayField(3);
    return {
      cmd: bk.ReadIntField(1),
      seq: bk.ReadIntField(2),
      data: bk.ReadArrayField(3),
    }
  }

  // return arraybuffer
  Encode: () => ArrayBuffer = () => {
    const bk = new WriteBlock()
    bk.WriteIntField(1, this.cmd)
    bk.WriteIntField(2, this.seq)
    bk.WriteArrayBufferField(3, this.data)
    return bk.getArrayBuffer()
  }
}
// connect pack
class ConnectReq {
  traceid: string;
  appid: string;
  cid: string;
  uid: string;
  pwd: string;
  data: ArrayBuffer;
  will: ArrayBuffer;
  version: string;
  platform: number;
  heartbeat: number;

  constructor(config: Connect) {
    this.traceid = get(config, 'traceid', '')
    this.appid = get(config, 'appid', '')
    this.cid = get(config, 'cid', '')
    this.uid = get(config, 'uid', '')
    this.pwd = get(config, 'pwd', '')
    this.data = get(config, 'data', new Uint8Array([]).buffer)
    this.will = get(config, 'will', new Uint8Array([]).buffer)
    this.version = get(config, 'version', '')
    this.platform = get(config, 'platform', 0)
    this.heartbeat = get(config, 'heartbeat', 0)
  }

  // return array
  Encode: () => ArrayBuffer = () => {
    const bk = new WriteBlock()
    bk.WriteStringField(1, this.traceid)
    bk.WriteStringField(2, this.appid)
    bk.WriteStringField(3, this.cid)
    bk.WriteStringField(4, this.uid)
    bk.WriteStringField(5, this.pwd)
    bk.WriteArrayBufferField(6, this.data)
    bk.WriteArrayBufferField(7, this.will)
    bk.WriteStringField(9, this.version)
    bk.WriteIntField(10, this.platform)
    bk.WriteIntField(11, this.heartbeat)
    return bk.getArrayBuffer()
  }
}
