// push pack
class PushReq {
  traceid: string;
  topic: string;
  ack: boolean;
  data: ArrayBuffer;
  extend: ArrayBuffer;

  constructor(config?: Push) {
    this.traceid = get(config, 'traceid', '')
    this.topic = get(config, 'topic', '')
    this.ack = get(config, 'ack', false)
    this.data = get(config, 'data', new Uint8Array([]).buffer)
    this.extend = get(config, 'extend', new Uint8Array([]).buffer)
  }

  // input array
  Decode: (buffer: ArrayBuffer) => {
    traceid: string;
    topic: string;
    ack: number;
    data: ArrayBuffer;
    extend: ArrayBuffer;
  } = buffer => {
    const bk = new ReadBlock(buffer)

    // this.traceid = bk.ReadStringField(1);
    // this.topic = bk.ReadStringField(2);
    // this.ack = bk.ReadIntField(3);
    // this.data = bk.ReadArrayField(4);
    return {
      traceid: bk.ReadStringField(1),
      topic: bk.ReadStringField(2),
      ack: bk.ReadIntField(3),  // TODO: boolean?
      data: bk.ReadArrayField(4),
      extend: new Uint8Array([]).buffer,
    }
  }

  // return array
  Encode: () => ArrayBuffer = () => {
    const bk = new WriteBlock()
    bk.WriteStringField(1, this.traceid)
    bk.WriteStringField(2, this.topic)
    bk.WriteBooleanField(3, this.ack)
    bk.WriteArrayBufferField(4, this.data)
    return bk.getArrayBuffer()
  }
}
