// bind pack
class BindReq {
  traceid: string;
  appid: string;
  cid: string;
  uid: string;
  pwd: string;
  data: ArrayBuffer;
  will: ArrayBuffer;

  constructor(config: Bind) {
    this.traceid = get(config, 'traceid', '')
    this.appid = get(config, 'appid', '')
    this.cid = get(config, 'cid', '')
    this.uid = get(config, 'uid', '')
    this.pwd = get(config, 'pwd', '')
    this.data = get(config, 'data', new Uint8Array([]).buffer)
    this.will = get(config, 'will', new Uint8Array([]).buffer)
  }

  // return array
  Encode: () => ArrayBuffer = () => {
    const bk = new WriteBlock()
    bk.WriteStringField(1, this.traceid)
    bk.WriteStringField(2, this.appid)
    bk.WriteStringField(3, this.cid)
    bk.WriteStringField(4, this.uid)
    bk.WriteStringField(5, this.pwd)
    bk.WriteArrayBufferField(6, this.data)
    bk.WriteArrayBufferField(7, this.will)
    return bk.getArrayBuffer()
  }
}

