import Event from 'events'
import get from 'lodash/get'
import {
  Controller,
} from '../Controller'
import {
  IM,
} from '../IM'
import {
  Topic,
} from '../../common/constant'
import {
  log,
} from '../../common/console'
import { NotifyId } from '../../common/NotifyId'

enum EventHandleType {
  MESSAGE_RECV = '1',
  NOTICE_RECV = '2',
  SINGLE_CMD_RECV = '3',
  ROOM_CMD_RECV = '4',
  MESSAGE_LIST_RECV = '5'
}

export default class MsgDistributor {
  private _im: IM;
  private _net: Controller;
  private _event: Event;
  constructor(im: IM, net: Controller) {
    this._net = net
    this._im = im
    this._event = new Event()

    this._run()
  }

  public onMessageRecv = (cb) => {
    this._event.on(EventHandleType.MESSAGE_RECV, cb)
  }

  public onMessageListRecv = (cb) => {
    this._event.on(EventHandleType.MESSAGE_LIST_RECV, cb)
  }

  public onNoticeRecv = (cb) => {
    this._event.on(EventHandleType.NOTICE_RECV, cb)
  }

  public onSingleCommandRecv = (cb) => {
    this._event.on(EventHandleType.SINGLE_CMD_RECV, cb)
  }

  public onRoomCommandRecv = (cb) => {
    this._event.on(EventHandleType.ROOM_CMD_RECV, cb)
  }

  private _run = () => {
    this._net.onRecv((apiPack, apiContext) => {
      if (apiContext.topic === Topic.IM_DATA) {
        this._msgRecvDeal(apiPack, apiContext)
        this._noticeRecvDeal(apiPack, apiContext)
      } else if (apiContext.topic === Topic.IM_CMD) {
        this._cmdRecvDeal(apiPack, apiContext)
      }
    })
  }

  private _cmdRecvDeal = (apiPack, apiContext) => {
    enum SingleMethods {
      singleCmdData,
    }

    enum RoomMethods {
      roomCmdData,
    }
    const apiPackData = apiPack.data
    if (apiPack.method in SingleMethods) {
      this._event.emit(EventHandleType.SINGLE_CMD_RECV, apiPackData, apiContext)
    } else if (apiPack.method in RoomMethods) {
      this._event.emit(EventHandleType.ROOM_CMD_RECV, apiPackData, apiContext)
    }
  }

  private _msgRecvDeal = (apiPack, apiContext) => {
    enum supportMethods {
      notify = 'notify',
      singleChat = 'singleChat',
      groupChat = 'groupChat',
      pullMessageNotify = 'pullMessageNotify',
      roomChat = 'roomChat',
    }

    switch (apiPack.method) {
      case supportMethods.singleChat:
        this._msgSingleChatDeal(apiPack, apiContext)
        break
      case supportMethods.pullMessageNotify:
        this._msgPullMessageNotifyDeal(apiPack, apiContext)
        break
      case supportMethods.groupChat:
        this._msgGroupChatDeal(apiPack, apiContext)
        break
      case supportMethods.notify:
        this._msgNotifyDeal(apiPack, apiContext)
        break
      case supportMethods.roomChat:
        this._msgRoomChatDeal(apiPack, apiContext)
        break
      default:
    }
  }

  private _noticeRecvDeal = (apiPack, apiContext) => {
    enum supportMethods {
      notify,
    }
    const apiData = apiPack.data
    const thisPlf = get(this._net, 'netBase.initInfo.platform')
    const willLogPlf = get(apiData, 'notify_data.platform')
    const kicked = `${apiData.notify_id}` === `${NotifyId.KICKED}`
    const samePlf = `${thisPlf}` === `${willLogPlf}`
    if (apiPack.method in supportMethods) {
      if (kicked && samePlf) { // 被踢事件手动退出登录
        this._im.logout()
      }

      this._event.emit(EventHandleType.NOTICE_RECV, apiData, apiContext)
    }
  }

  /**
   * 收到服务端主动推送的单聊信息，信息没有积压的情况，返回给服务端确认
   * @param {object} apiPack 收到消息推送的API相关信息
   * @param {object} apiContext 收到的上下文信息
   * @param {function} cb 收到信息的回调函数
   */
  private _msgSingleChatDeal (apiPack, apiContext) {
    log('_MSSDK_IM_ _msgSingleChatDeal apiPack:', apiPack)
    const apiPackData = apiPack.data
    this._im._saveSeqMap(apiPackData.from_id, apiPackData.seq_id)
    /**
     * 此时回一个响应请求即可
     */
    if (+apiPackData.msg_type !== 13 && +apiPackData.msg_type !== 4) {
      this._im._pullSingleMessage({
        data: {
          traceid: apiContext.traceid,
          size: 0,
          uid: apiPackData.from_id,
          seq_id: this._im._getSeqMap(apiPackData.from_id),
          msg_id: [apiPackData.msg_id],
        },
      })
    }

    this._event.emit(EventHandleType.MESSAGE_RECV, apiPackData, apiContext)
  }

  public _msgPullMessageNotifyDeal (apiPack, apiContext) {
    enum PullMsgType {
      SINGLE_CHAT = 1,
      GROUP_CHAT = 2,
      CHATROOM_CHAT = 3
    }
    const apiPackData = apiPack.data
    if (apiPackData.target_type === PullMsgType.SINGLE_CHAT) {
      this._pullMessageNotifySingleDeal(apiPack, apiContext)
    } else if (apiPackData.target_type === PullMsgType.GROUP_CHAT) {
      this._pullMessageNotifyGroupDeal(apiPack, apiContext)
    } else if (apiPackData.target_type === PullMsgType.CHATROOM_CHAT) {
      this._pullMessageNotifyChatroom(apiPack, apiContext)
    }
  }

  /**
   * 收到服务端主动推送的单聊信息，信息积压的情况，实现一套实时拉取的逻辑
   * @param {object} apiPack 收到消息推送的API相关信息
   * @param {object} apiContext 收到的上下文信息
   * @param {function} cb 收到信息的回调函数
   */
  private _pullMessageNotifySingleDeal (apiPack, apiContext) {
    log('_MSSDK_IM_ _pullMessageNotifySingleDeal apiPack:', apiPack, apiContext)
    const apiPackData = apiPack.data
    this._pullSingleMessageLoop(apiPackData.target_id, apiPackData.size, [])
  }
  // TODO: 似乎跟IM.ts里的方法重复？？？群聊也是
  /**
   * 递归拉取单聊数据
   * @param {string} uid 要拉取的目标uid
   * @param {number} unread 未读消息数
   * @param {array} msg_ids 已经收到的消息id数组，用于给服务端确认已经收到了这些消息，可以从缓存中删除
   * @param {function} cb 每条消息的回调函数
   * @param {string} traceid 透传的traceid，非必填
   */
  private _pullSingleMessageLoop (uid, unread, msg_ids, traceid?) {
    log('_MSSDK_IM_ _pullSingleMessageLoop arguments:', uid, unread, msg_ids)

    this._im._pullSingleMessage({
      data: {
        traceid: traceid,
        uid: uid,
        seq_id: this._im._getSeqMap(uid),
        size: unread,
        msg_id: msg_ids,
      },
      onSuccess: (apiData, context) => {
        log('_MSSDK_IM_ _pullSingleMessage onSuccess:', apiData, context)
        const messages = apiData.data
        messages.length && this._im._saveSeqMap(apiData.uid, messages[messages.length - 1].seq_id)

        if (apiData.unread === 0) {
          this._im._pullSingleMessage({
            data: {
              traceid: context.traceid,
              uid: apiData.uid,
              seq_id: this._im._getSeqMap(apiData.uid),
              size: 0,
              msg_id: messages.map(msg => msg.msg_id),
            },
          })
        } else if (apiData.unread > 0) {
          this._pullSingleMessageLoop(apiData.uid, apiData.unread, apiData.data.map((message) => message.msg_id), context.traceid)
        }
        messages.map((message) => {
          this._event.emit(EventHandleType.MESSAGE_RECV, message, context)
        })
      },
      onFail: (apiData, context) => {
        log('_MSSDK_IM_ _pullSingleMessage onFail:', apiData, context)
      },
      onTimeout: (apiData, context) => {
        log('_MSSDK_IM_ _pullSingleMessage onTimeout:', apiData, context)
      },
    })
  }

  // TODO: 问题：服务端什么场景下会发送这个消息
  /**
   * 收到服务端主动推送的群聊信息，信息积压的情况，实现一套实时拉取的逻辑
   * @param {object} apiPack 收到消息推送的API相关信息
   * @param {object} apiContext 收到的上下文信息
   * @param {function} cb 收到信息的回调函数
   */
  private _pullMessageNotifyGroupDeal (apiPack, apiContext) {
    log('_MSSDK_IM_ _pullMessageNotifyGroupDeal apiPack:', apiPack, apiContext)
    const apiPackData = apiPack.data

    this._pullGroupMessageLoop(apiPackData.target_id, apiPackData.size)
  }

  /**
   * 收到服务端主动推送的聊天室消息，信息积压的情况，实现一套实时拉取的逻辑
   * @param pack 消息推送的API相关信息
   * @param ctx 上下文
   */
  private _pullMessageNotifyChatroom (apiPack, apiContext) {
    log('-------chatroom msg pull---------:', apiPack, apiContext)
    const apiPackData = apiPack.data
    this._pullRoomMessageLoop(apiPackData.target_id, apiPackData.size)
  }

  /**
   * 递归拉取群聊数据
   * @param {string} gid 需要拉取的群ID
   * @param {number} unread 未读消息数
   * @param {function} cb 每条消息的回调函数
   * @param {string} traceid 透传的traceid，非必填
   */
  private _pullGroupMessageLoop (gid, unread, traceid?) {
    log('_MSSDK_IM_ _pullGroupMessageLoop arguments:', gid, unread)

    this._im._pullGroupMessage({
      data: {
        traceid: traceid,
        gid: gid,
        seq_id: this._im._getSeqMap(gid),
        size: unread,
      },
      onSuccess: (apiData, context) => {
        log('_MSSDK_IM_ _pullGroupMessage onSuccess:', apiData, context)
        const messages = apiData.data
        messages.length && this._im._saveSeqMap(apiData.gid, messages[messages.length - 1].seq_id)

        if (apiData.unread === 0) {
          this._im._pullGroupMessage({
            data: {
              traceid: context.traceid,
              gid: apiData.gid,
              seq_id: this._im._getSeqMap(apiData.gid),
              size: 0,
            },
          })
        } else if (apiData.unread > 0) {
          this._pullGroupMessageLoop(apiData.gid, apiData.unread, context.traceid)
        }
        messages.map((message) => {
          this._event.emit(EventHandleType.MESSAGE_RECV, message, context)
        })
      },
      onFail: (apiData, context) => {
        log('_MSSDK_IM_ _pullGroupMessage onFail:', apiData, context)
      },
      onTimeout: (apiData, context) => {
        log('_MSSDK_IM_ _pullGroupMessage onTimeout:', apiData, context)
      },
    })
  }

  private _msgGroupChatDeal (apiPack, apiContext) {
    log('_MSSDK_IM_ _msgGroupChatDeal apiPack:', apiPack)
    const apiPackData = apiPack.data
    this._im._saveSeqMap(apiPackData.target_id, apiPackData.seq_id)

    /**
     * 此时回一个响应请求即可
     */
    if (+apiPackData.msg_type !== 13 && +apiPackData.msg_type !== 4) {
      this._im._pullGroupMessage({
        data: {
          traceid: apiContext.traceid,
          gid: +apiPackData.target_id,
          seq_id: this._im._getSeqMap(apiPackData.target_id),
          size: 0,
        },
      })
    }

    this._event.emit(EventHandleType.MESSAGE_RECV, apiPackData, apiContext)
  }

  private _msgNotifyDeal (apiPack, apiContext) {
    const apiData = apiPack.data

    // 离线消息拉取通知
    if (apiData.notify_id === 1) {
      if (apiData.notify_data.single && apiData.notify_data.single.length) {
        const list = apiData.notify_data.single

        list.map((session) => {
          this._im._saveSeqMap(session.uid, session.seq_id)
          this._pullSingleMessageLoop(session.uid, session.size, [])
        })
      }
      if (apiData.notify_data.group && apiData.notify_data.group.length) {
        const list = apiData.notify_data.group

        list.map((session) => {
          this._im._saveSeqMap(session.gid, session.seq_id)
          this._pullGroupMessageLoop(session.gid, session.size)
        })
      }
    }
  }

  /**
   * 收到服务端主动推送的聊天室信息，信息没有积压的情况，返回给服务端确认
   * @param {object} apiPack 收到消息推送的API相关信息
   * @param {object} apiContext 收到的上下文信息
   * @param {function} cb 收到信息的回调函数
   */
  private _msgRoomChatDeal (apiPack, apiContext) {
    // 收到聊天室消息
    log('_MSSDK_IM_ _msgRoomChatDeal apiPack:', apiPack)
    const apiPackData = apiPack.data
    this._im._saveSeqMap(apiPackData.from_id, apiPackData.seq_id)

    /**
     * 此时回一个响应请求即可
     */
    if (+apiPackData.msg_type !== 13 && +apiPackData.msg_type !== 4) {
      this._im._pullRoomMessage({
        data: {
          traceid: apiContext.traceid,
          roomid: +apiPackData.target_id,
          seq_id: this._im._getSeqMap(apiPackData.target_id),
          size: 0,
        },
      })
    }
    this._event.emit(EventHandleType.MESSAGE_RECV, apiPackData, apiContext)
  }

  // TODO： 添加抛出事件，用于上抛未读消息列表
  private _pullRoomMessageLoop (roomid, unread, traceid?) {
    log('_MSSDK_IM_ _pullRoomMessageLoop arguments:', roomid, unread)

    this._im._pullRoomMessage({
      data: {
        traceid: traceid,
        roomid: +roomid,
        seq_id: this._im._getSeqMap(roomid),
        size: unread,
      },
      onSuccess: (apiData, context) => {
        log('_MSSDK_IM_ _pullRoomMessageLoop onSuccess:', apiData, context)
        const messages = apiData.data
        //  遍历设置seq_id，根据设置的结果来决定是否处理该消息，是否上抛
        const filteredMsgList = messages.filter((msg) => this._im._saveSeqMap(apiData.roomid, msg.seq_id))

        if (apiData.unread === 0) {
          this._im._pullRoomMessage({
            data: {
              traceid: context.traceid,
              roomid: apiData.roomid,
              seq_id: this._im._getSeqMap(apiData.roomid),
              size: 0,
            },
          })
        } else if (apiData.unread > 0) {
          this._pullRoomMessageLoop(apiData.roomid, apiData.unread, context.traceid)
        }

        // v4.4.1新增，将拉取的列表数据以列表形式抛出，上层可用于做消息去重
        if (filteredMsgList.length) {
          if (filteredMsgList.length > 1) {
            this._event.emit(EventHandleType.MESSAGE_LIST_RECV, filteredMsgList, context)
          } else if (filteredMsgList.length === 1) {
            this._event.emit(EventHandleType.MESSAGE_RECV, filteredMsgList[0], context)
          }
        }
      },
      onFail: (apiData, context) => {
        log('_MSSDK_IM_ _pullRoomMessageLoop onFail:', apiData, context)
      },
      onTimeout: (apiData, context) => {
        log('_MSSDK_IM_ _pullRoomMessageLoop onTimeout:', apiData, context)
      },
    })
  }
}
