import uuid from 'uuid/v1'
import { Topic } from '../../common/constant'
import { json2ArrayBuffer } from '../../common/helper'
import { getCmdBinArr } from '../../common/protobuf/protoUtil'
import { ApiPack } from '../../common/types/core'
import { Call } from '../../common/types/rtmq'
import { log } from '../../common/console'
import { ControllerRequest, ApplyRequest } from '../../common/types/apply'
import pick from 'lodash/pick'
import get from 'lodash/get'
interface CreateConfig {
  method: string;
  topic?: Topic;
  data: ApplyRequest;
}

/**
 * 生成call方法的请求体
 * @param conf {CreateConfig}
 * @returns {ControllerRequest}
 */
export const createCallReq: (conf: CreateConfig) => ControllerRequest = (conf) => {
  const originData = get(conf, 'data.data', {})
  const postData: ApiPack = {
    method: conf.method,
    time: new Date().getTime(),
    data: originData,
  }

  const topic = get(conf, 'topic', Topic.IM_DATA)

  const rtmqData: Call = {
    traceid: get(conf, 'data.traceid', uuid()), // TODO: traceid不应该放在data里面
    topic: topic,
    data: topic === Topic.IM_CMD ? getCmdBinArr(conf.method, originData) : json2ArrayBuffer(postData),
  }

  log(`_MSSDK_IM_ ${conf.method} rtmqData: `, rtmqData, 'postData: ', postData)

  const cbs = pick(conf.data, ['onSuccess', 'onFail', 'onTimeout'])

  return { rtmqData, ...cbs }
}
