/** **
 * 这个主要是封装Push 对外的类
 * 类上挂在的方法主要来自于MisPushExtension
 */
import {
  log,
} from '../../common/console'
import {
  Controller,
} from '../Controller'
import uuid from 'uuid/v1'
import {
  Topic,
} from '../../common/constant'
import { getVariableType, json2ArrayBuffer } from '../../common/helper'

export class Push {
  isInit: boolean;
  controller: Controller;
  _seqMap: any;

  constructor() {
    this.isInit = false // 看是不是初始化了
    this.controller = new Controller()
    this._seqMap = null
  }

  /**
   * 初始化基本数据
   * @param {object} initInfo 基本数据
   */
  init(initInfo) {
    Object.assign(initInfo, {
      authInfo: {
        auth: initInfo.auth,
      },
    })
    this.controller.init(initInfo).start()
    this.isInit = true
    return this
  }

  /**
   * 是否正在连接
   */
  getIsConnected() {
    return this.controller.getIsConnected()
  }

  onConnect(fn) {
    this.controller.onConnect(fn)
    return this
  }

  onDisconnect(fn) {
    this.controller.onDisconnect(fn)
    return this
  }

  /**
   * 监听Push消息
   * @param {function} cb 回调函数
   */
  onPushMessage(cb) {
    this.controller.onRecv((apiPack, apiContext) => {
      log('_MSSDK_Push_ onPushMessage onRecv :', apiPack, apiContext)
      if (apiContext.topic === Topic.PUSH_DATA) {
        if (apiPack.method === 'notify') {
          if (apiPack.data.notify_id === 1) { // 设备拉取事件
            if (apiPack.data.notify_data && apiPack.data.notify_data.message) {
              this._recvNotifyMessage(apiPack, apiContext)
            }
          }
        } else if (apiPack.method === 'message' || apiPack.method === 'offMessage') {
          this._recvMessage(apiPack, apiContext, cb)
        }
      }
    })
    return this
  }

  stopPush() {

    return this
  }

  resumePush() {

    return this
  }

  isPushStopped() {

    return this
  }

  openMessage() {

    return this
  }

  /**
   * 设置别名
   * @param {object} param0 config
   */
  setAlias({
    data,
    onSuccess,
    onFail,
    onTimeout,
  }) {
    const postData = {
      method: 'setAlias',
      time: new Date().getTime(),
      data: {
        alias: data.alias,
      },
    }
    log('_MSSDK_Push_ setAlias rtmqData', postData)
    const rtmqData = {
      traceid: uuid(), // TODO: traceid不应该放在data里面
      topic: Topic.PUSH_DATA,
      data: json2ArrayBuffer(postData),
    }
    log('_MSSDK_Push_ setAlias rtmqData', rtmqData)
    this.controller.call({ rtmqData, onSuccess, onFail, onTimeout })
  }

  setTags() {

    return this
  }

  addTags() {

    return this
  }

  deleteTags() {

    return this
  }

  cleanTags() {

    return this
  }

  getTags() {

    return this
  }

  _pullMessage({
    data,
    onSuccess,
    onFail,
    onTimeout,
  }) {
    const postData = {
      method: 'pullMessage',
      time: new Date().getTime(),
      size: data.size,
      data: {
        seq_id: data.seq_id,
      },
    }
    const rtmqData = {
      traceid: uuid(),
      topic: Topic.PUSH_DATA,
      ack: true,
      data: json2ArrayBuffer(postData),
    }
    log('_MSSDK_Push_ _pullMessage rtmqData', rtmqData)

    this.controller.send({ rtmqData, onSuccess, onFail,onTimeout})
  }

  _emitServerPushMessage(seq_id) {
    log('_MSSDK_Push_ _emitServerPushMessage enter seq_id:', seq_id)

    this._pullMessage({
      data: {
        size: 1,
        seq_id,
      },
      onSuccess: (apiData, apiContext) => {
        log('_MSSDK_Push_ _emitServerPushMessage onSuccess code=0:', apiData, apiContext)
      },
      onFail: (data) => {
        log('_MSSDK_Push_ _emitServerPushMessage onFail data:', data)
      },
      onTimeout: () => {
        log('_MSSDK_Push_ _emitServerPushMessage onTimeout')
      },
    })
  }

  _recvNotifyMessage(apiPack, apiContext) {
    log('_MSSDK_Push_ _recvNotifyMessage enter:', apiPack, apiContext)
    const {
      seq_id,
      size,
    } = apiPack.data.notify_data.message

    if (size < 1) {
      return
    }

    this._emitServerPushMessage(seq_id)
  }

  _recvMessage(apiPack, apiContext, cb) {
    log('_MSSDK_Push_ _recvMessage enter:', apiPack, apiContext)

    if(!(apiPack.data.content && apiPack.data.content.length)) {
      return
    }
    let message = apiPack.data.content[0]

    if(getVariableType(message) === 'String') {
      message = JSON.parse(message)
    }
    cb && cb(message, apiContext)

    const seq_id = apiPack.data.content[0].msg_id
    this._emitServerPushMessage(seq_id)
  }
}
