import {
  getQueryVariable,
}
  from './helper'

export const log = function (...args) {
  // TODO:这个参数要写进文档里
  const urlIsLog = getQueryVariable('seewoMessageLog') === 'true'
  const windowIsLog = window['__seewoMessageLog__']

  const isLog = urlIsLog || windowIsLog || false // 是否打印底层日志
  isLog && console.log.apply(console, args)
}

export const error = console.error.bind(console)
